@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* "#eb4034" */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

header {
  width: 100%;
  padding: 1rem;
  background-color: #eb4034;
  color: white;
  font-size: 2rem;
  text-align: center;
}

.pokemon-grid {
  width: 100%;
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}


.Link, .Link:hover, .Link:focus, .Link:active, .Link:visited {
  color: black;
  text-decoration: none;
}

.pokemon-card {
  width: 250px;
  /* width: 100%; */
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items:center;
          align-items:center;
  border: 1px solid rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  margin: 5px;
  overflow: hidden;
  padding: 10px;
  cursor: pointer;
  transition: all 0.1s linear;
}

@media (max-width: 580px) {
  .pokemon-card {
    width: 80vw;
  }
}
.pokemon-card:hover {
  -webkit-transform: translateY(-5px);
          transform: translateY(-5px);
  box-shadow: 0 10px 5px rgba(0, 0, 0, 0.1);
}

.pokemon-card img {
  margin: 5px;
  height: 200px;
  max-width: 100%;
  /* height: 20%; */
}

.number, .type-div {
  width: 100%;
  text-align: left;
  font-size: 1rem;
}

.pokemon-name {
  width: 100%;
  margin-bottom: 10px;
  font-size: 1.5rem;
  text-transform: capitalize;
}

.type {
  display: inline-block;
  font-size: 1rem;
  border-radius: 1000px;
  background-color: #eb4034;
  padding: 5px 20px;
  margin: 0px 10px 10px 0px;
  color: white;
}

.pagination {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.load {
  background-color: #eb4034;
  color: white;
  padding: 5px 25px;
  outline: none;
  border: none;
  border-radius: 1000px;
  margin-bottom: 20px;
  cursor: pointer;
  transition: all 0.2s linear;
  font-size: 1.1rem;
}

.load:active {
  -webkit-transform: translateY(5px);
          transform: translateY(5px);
}

.bg-div{ 
  max-width: 100vw;
  min-height: calc(100vh - 64px);
  padding: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.main-div{
  max-width: 350px;
  width: 100%;
  padding: 20px;
  border: 1px solid #eb4034;
  margin: 20px auto;
  border-radius: 10px;
  /* margin: auto; */
}

.img-name-details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

.img-name-details img {
  max-width: 100%;
}

.name-details {
  margin: 20px 0 0px 0px;
  font-weight: 300;
  text-transform: capitalize;
}

h2 {
  font-weight: 500;
  font-size: 2.5rem;
}

p {
  text-align: justify;
  font-size: 1rem;
}

.multiplier>h4 {
  margin: 5px;
  font-size: 1.1rem;
  font-weight: 300;
}

.info {
  margin-top: 10px;
  text-align: center;
}

#footer {
  background-color: #eb4034;
  padding: 1rem;
  color: white;
  text-align: center;
  font-size: 1.2rem;
}

#footer a {
  color: white;
}
